<template>
  <div class="home">
    <DefaultTheme>
      <Gantt :tasks="tasks" :links="links" :scales="scales" :columns="columns" />
    </DefaultTheme>
    <button @click="tasks = testTask">Load Data</button>
  </div>
</template>

<script>
import { Gantt, DefaultTheme } from "@dhtmlx/trial-vue-gantt";
import { columns, scales, tasks, links } from "../data";
import ganttConfig from './gantt.config.json';


export default {
  name: 'Home',
  components: {
    Gantt,
    DefaultTheme
  },
  data() {
    return {
      columns,
      scales,
      tasks,
      links,
      form: {
        begin: '2021-09-01',
        end: '2021-11-01',
        page: 1
      },
      dataSource: ganttConfig.data,
      testTask: [],
    }
  },
  // computed: {
  //   tasks() {
  //     return this.task
  //   }
  // },
  created() {
    this.mapData();
    console.log(tasks, 'tasks')
  },
  methods: {
    mapData() {
      const data = this.dataSource.filter((el) => el.organizer !== '').map((item, index) => {
        return {
          id: index+1,
          open: true,
          start_date: item.begin,
          end_date: item.end,
          text: item.organizer,
        }
      });
      console.log(data, 'data');
      let arr = [];
      data.forEach((item) => {
        if (item.text !== '') {
          arr.push({
            ...item,
            id: item.id+7,
            parent: item.id
          })
        }
      })
      this.testTask = data.concat(arr);
      console.log(this.testTask, 'tasks')
    },
  },
};
</script>

<style lang="sass">
.home
  height: 100vh 
</style>